    {name: 'Home', routerName: 'Home'},
    {name: 'Resume', routerName: 'Resume'},
    {name: 'About Me', routerName: 'About'}
  ]
  to (routerName: any) {
    this.$router.push({ name: routerName })
  }
  mounted () {
    console.log(this.$route)
  }
}



