



















import Vue from 'vue'
import Component from 'vue-class-component'

@Component({
  props: {
    id: {
      required: true,
      type: Number
    },
    name: {
      required: true,
      type: String
    },
    title: {
      required: true,
      type: String
    },
    subTitle: {
      type: String
    },
    preview: {
      required: true,
      type: String
    },
    author: {
      required: true,
      type: String
    },
    createdTime: {
      required: true,
      type: String
    }
  }
})
export default class ArticleItem extends Vue {
  onClick () {
    this.$router.push({name: this.$props.name})
  }
}



